html {
  overflow-x: hidden;
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 0px;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
  background-color: #001331;
  color: white;
  overflow-x: hidden;
  overflow-y: hidden;
}

