/*NavBar*/
.header {
  width: 75%;
  background-color: rgba(15, 23, 42, 0.9);
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0px 20px;
  z-index: 1;
  margin: 0 auto;
  width: 100%;
}

h1 {
  margin: 0;
  font-family: "Courier New";
  left: 17%;
  top: 7px;
  color: #ffffff;
}

.header ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 400px;
  height: 100%;
  margin: 0;
}

.header ul li {
  list-style: none;
  font-weight: bold;
}

.header nav {
  height: 100%;
}

@media (max-width: 450px) {
  .header nav {
    width: 20%;
  }
}
.header nav div {
  font-size: 25px;
  position: relative;
  top: -40px;
  text-align: right;
  visibility: hidden;
}

@media (max-width: 890px) {
  .header nav div {
    visibility: visible;
  }
}
.header nav div a {
  color: #58bff4;
}

.header nav div a:hover {
  color: #d5e3f5;
}

.header ul li a {
  text-decoration: none;
  color: #93c5fd;
  font-family: "Courier New", Courier, monospace;
  font-size: 15px;
}

.header ul li a:hover {
  color: #d5e3f5;
}

.nav-icons {
  visibility: hidden;
}

